import { Component, OnInit } from '@angular/core';
import { HtmlEncodeService } from '../html-encode.service';
@Component({
  selector: 'app-sizes',
  templateUrl: './sizes.component.html',
  styleUrls: ['./sizes.component.scss']
})
export class SizesComponent implements OnInit {

  mediaQueryCode = `
  // Importera _settings.sizes.scss frn komponentkartan.
  @import '../../../node_modules/vgr-komponentkartan/assets/partials/_settings.sizes.scss';

  // Stt default-storlek utan media query. Detta r den lilla storleken.
  .sizeInfo {
    &:after {
      content: "Liten";
      height: auto;
      width: auto;
      font-weight: bold;
    }
  }

  // Definiera media query baserat p variabel fr stor storlek.
  @media screen and (min-width: $desktop-width--medium) {
    .sizeInfo {
      &:after {
        content: "Stor";
      }
    }
  }
  `;

  mediaQueryExample: string;
  constructor(htmlEncoder: HtmlEncodeService) {
    this.mediaQueryExample = htmlEncoder.prepareHighlightedSection(this.mediaQueryCode, 'scss');
  }

  ngOnInit() {
  }

}
